<template>
        <div class="search-wrapper">
            <input type="text" class="search" placeholder="Search for a champion" v-model="search">
            <div v-if="searchResults.length > 0 && search" class="search-container">
                <span v-for="result in searchResults" class="search-result" :key="result" @click="openChanges(result)">{{ result }} </span>
            </div>
        </div>
</template>

<script setup>
import { computed, ref } from 'vue';
import { useChanges } from '../store/index'
import { useRouter } from 'vue-router'


const changes = useChanges()
const router = useRouter()

const hero = ref(null)
const search = ref('')


const searchResults = computed(()=>{
    console.log(changes.changes)
    return Object.keys(changes.changes).filter((key) =>{
        if(key.toLowerCase().includes(search.value.toLowerCase()) && key != 'LatestGlobalPatch'){
            return key
        } 
    })
}) 

function openChanges(result){
    router.push({ path: '/changes', query: { champ: result } })
    search.value = ''
}


defineExpose({hero})
</script>

<style scoped>
.search{
    background-color: var(--main-app-ad-bg-color);
    border-radius: 3rem;
    border: none;
    height: 3rem;
    font-size: 18px;
    padding-left: 2rem;
    box-sizing: border-box;
    color: var(--main-app-white-text-color);
    width: 100%;
}
.search:focus{
    outline: none;
}
.search-wrapper{
    position: relative;
}
.search-container{
    background-color: var(--main-app-ad-bg-color);
    display: flex;
    flex-direction: column;
    position: absolute;
    width: 100%;
    margin-top: 0.5rem;
    border-radius: 3rem;
    padding: 2rem 0rem;
    box-sizing: border-box;
    max-height: 30rem;
    overflow: auto;
    z-index: 99;
}
.search-result{
    padding: 0.5rem 2rem;
}
.search-result:hover{
    background-color: var(--main-app-ui-element-hover-color);
    cursor: pointer;
}
</style>
