<template>
      <div class="tabs" style="height: 40px; width: 360px;">
        <div class="single-champ-tab-container" style="width: 357px; height: 30px;">
            <template v-for="tab in tabs" :key="tab.name"> 
                <div :class="{'single-champ-tab-inactive': page != tab.name}" :style="{width: tab.width}" @click="changePage(tab.name)">{{ tab.name }}</div>
                <span style="width: 1px;height: 100%;background: var(--main-app-cta-color);"></span>
            </template>

        </div>
      </div>
</template>

<script setup>
import {ref, onMounted} from 'vue'
const emit = defineEmits(['pageChange'])

const page = ref('')
const props = defineProps({tabs:[]})

function changePage(newPage){
    emit('pageChange', newPage)
    page.value = newPage
    console.log(page.value)
}

</script>

<style>
.tabs > .single-champ-tab-container div{
    width: 120px;
}
.single-champ-tab-container{
    font-size: 15px;
    display: flex;
    width: 201px;
    border-radius: 3px;
    overflow: hidden;
}
.single-champ-tab-container > div{
    color: var(--main-app-white-text-color);
    background-color: var(--main-app-secondary-color);
    box-sizing: border-box;
    padding: 5px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    border: 1px solid var(--main-app-secondary-color);
}
.single-champ-tab-container > div.single-champ-tab-inactive{
    background-color: transparent;
}
.single-champ-tab-container > div:hover{
    background-color: var(--main-app-ui-element-hover-color);
    opacity: 1;
    border: none;
    cursor: pointer;
}
.single-champ-tab-inactive{
    opacity: 0.6;
    border: 1px solid var(--main-app-secondary-color);
}
.single-champ-tab-container span:last-of-type{
    display: none;
}
</style>
