import { defineStore } from 'pinia'
import { onMounted, ref } from 'vue'


export const useChanges = defineStore('changes', () => {
    const changes = ref({})

    async function loadChanges(){
        const res = await fetch('changes/changes.json');
        changes.value = await res.json();
        console.log('mounted store and fetched json')
    }

    return {
        changes,
        loadChanges
    }
})