<template>
    <Header/>
    <div class="hero-container" ref="hero1">
        <HeroSection key="1" class="main-card"/> 
        <div class="download-card" >Download the full app on {Overwolf logo here} to enjoy full features <button>Download Free {windows/overwolf logos}</button></div>
        <div class="contact-card">Contact</div>
        <div class="about-card">About</div>
    </div>
    <div class="check-more">
      <span>Check more features</span>
      <IconRender iconName="downArrowIcon"/>
    </div>
    <div class="live-container hero-fade-in"  ref="hero2">
      <h1>Lobby and In Game, always have the information needed at your fingertips</h1>
      <Tabs :tabs="[{name:'Lobby', width:'105px'}, {name:'In Game', width:'110px'}]" @pageChange="e => page = e"/>
      <div class="live-container-inside" v-if="page=='Lobby'">
        <h2>Lobby</h2>
        <div class="live-container-two-images">
          <img src="../assets/lobby-1.png" alt="" srcset="">
          <img src="../assets/lobby-2.png" alt="" srcset="">

        </div>
      </div>
      <div class="live-container-inside" v-if="page=='In Game'">
        <h2>In Game ARAM</h2>
        <img src="../assets/in-game-aram-1.png" alt="" srcset="">
      </div>
    </div>
    <div class="items-container hero-fade-in" ref="hero3">
      <h1>Accurate and Up-to-Date information</h1>
      <h3>Spells are shown just like the in game tooltips, with all the ratios and numbers</h3>
    </div>
    <div class="items-container hero-fade-in" ref="hero4">
      <h1>Items</h1>
    </div>

</template>

<script setup>

import Header from '@components/Header.vue';
import HeroSection from '@components/HeroSection.vue';
import IconRender from '../components/IconRender.vue';
import Tabs from '../components/Tabs.vue';
import { useChanges } from '../store/index'
import { onMounted, ref } from 'vue';

const changes = useChanges()
const hero1 = ref(null)
const hero2 = ref(null)
const hero3 = ref(null)
const hero4 = ref(null)

const page = ref('')

function observerCallback(entries) {
  entries.forEach(entry => {
    if (entry.isIntersecting) {
      // fade in observed elements that are in view
      entry.target.classList.replace('hero-fade-out', 'hero-fade-in')
    } else {
      // fade out observed elements that are not in view
      entry.target.classList.replace('hero-fade-in', 'hero-fade-out')
    }
  });
}
function changePage(page){
  console.log(page)
  page.value = page
}
onMounted(()=>{
    const observer = new IntersectionObserver(observerCallback, {threshold: 0.2})

    const heroSections = [hero1, hero2, hero3, hero4]
    console.log(hero1.value)
    console.log(hero2.value)

    heroSections.forEach(el => {console.log(el.value); observer.observe(el.value)})
  })
</script>

<style scoped>
.check-more{
  margin-top: 3rem;
}


.live-container{
  margin-top: 5rem;
  transition: opacity 0.5s;
  width: 65%;
}
.live-container-inside{
  display: flex;
  flex-direction: column;
}
.live-container-two-images{
  display: flex;
  justify-content: center;
}
.live-container-two-images img{
  width: 82%;
  height: 51%;
}
.items-container{
  transition: opacity 0.5s;
}
</style>
